.stopwatch {
  justify-content: center;
  align-items: center;
  height: 100vh;
  display: flex;
}

.stopwatch__minutes-hand {
  transform-origin: bottom;
  background-color: #0700ff;
  width: 3vmin;
  height: 20vmin;
  animation: 3600s steps(60, end) infinite forwards rotation;
  position: absolute;
  bottom: 50.25%;
  left: 48.15%;
}

.stopwatch__seconds-hand {
  transform-origin: bottom;
  background-color: #2c8000;
  width: 1.5vmin;
  height: 38vmin;
  animation: 60s linear infinite forwards rotation;
  position: absolute;
  bottom: 50.15%;
  left: 49%;
}

.stopwatch__circle {
  z-index: 1;
  background-color: #f6a603;
  border-radius: 50%;
  width: 5vmin;
  height: 5vmin;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}

.stopwatch__container {
  border: 1vmin dotted #000;
  border-radius: 50%;
  width: 80vmin;
  height: 80vmin;
  display: flex;
  position: relative;
}

.stopwatch__container--speed-up .stopwatch__minutes-hand {
  animation: 600s steps(60, end) infinite forwards rotation;
}

.stopwatch__container--speed-up .stopwatch__seconds-hand {
  animation: 10s linear infinite forwards rotation;
}

@keyframes rotation {
  100% {
    transform: rotate(1turn);
  }
}

